
import { defineComponent } from "vue";

export default defineComponent({
  name: "Perfil",
  methods: {
    logoff() {
      this.$store.commit("signout");
      this.$router.push("/login");
    }
  },
  computed: {
    name(): string {
      return this.$store.state.user.name;
    },
    isAdmin(): boolean {
      return this.$store.state.user.is_admin;
    }
  }
});
